@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,400i,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  line-height: 1.5715;
  -webkit-font-feature-settings: 'tnum';
  font-feature-settings: 'tnum', "tnum";
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0.5em;
  color: rgba(0, 0, 0, 0.85);
  font-weight: 500;
}

*,
*:after,
*:before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1200px;
  padding: 0 15px;
  margin: 0 auto;
}

.d-block {
  display: block;
}

.intro {
  text-align: center;
}

.options {
  display: flex;
  justify-content: center;
}

.options__item {
  padding: 0 15px;
}

.file_select {
  margin-top: 60px;
  display: flex;
  justify-content: center;
}

.submit {
  margin-top: 12px;
  padding-bottom: 30px;
  display: flex;
  justify-content: center;
}

.submit input {

}
.download {
  display: flex;
  justify-content: center;
}

.btn {
  min-width: 120px;
  height: 40px;
}

.lang_tab a {
  margin-right: 12px;
}

.ghost {
  opacity: 0;
  transition: opacity .5s linear;
}

.ghost-show {
  opacity: 1;
}

.header {
  display: flex;
  justify-content: space-between;
  padding: 0 15px;
}

:root {
    --colorShadeA: rgba(50, 50, 50);
    --colorShadeB: rgb(150, 150, 150);
    --colorShadeC: rgb(190, 190, 190);
    --colorShadeD: rgb(210, 210, 210);
    --colorShadeE: rgb(230, 230, 230);
}

button {
    position: relative;
    display: inline-block;
    cursor: pointer;
    outline: none;
    vertical-align: middle;
    text-decoration: none;
    font-size: 1rem;
    color:rgba(50, 50, 50);
    color:var(--colorShadeA);
    font-family: inherit;

    margin: 6px 3px;
    padding: 3px 6px;
    border: 2px solid rgb(150, 150, 150);
    border: 2px solid var(--colorShadeB);
    border-radius: 9px;
    background: rgb(230, 230, 230);
    background: var(--colorShadeE);
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    transition: all 125ms cubic-bezier(0, 0, 1, 1);
}

button.big-button::before {
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgb(190, 190, 190);
    background: var(--colorShadeC);
    border-radius: inherit;
    box-shadow: 0 0 0 1px rgb(150, 150, 150), 0 2px 0 0 rgb(150, 150, 150);
    box-shadow: 0 0 0 1px var(--colorShadeB), 0 2px 0 0 var(--colorShadeB);
    -webkit-transform: translate3d(0, 5px, -1em);
            transform: translate3d(0, 5px, -1em);
    transition: all 175ms cubic-bezier(0, 0, 1, 1);
}


button.big-button.toggled,
button.big-button:hover {
    background: rgb(190, 190, 190);
    background: var(--colorShadeC);
}


button.big-button.toggled {
    -webkit-transform: translate(0em, 7px);
            transform: translate(0em, 7px);
}

button.big-button.toggled::before {
    -webkit-transform: translate3d(0, 0, -1em);
            transform: translate3d(0, 0, -1em);
    box-shadow: 0 0 0 1px rgb(150, 150, 150), 0 2px 0 0 rgb(150, 150, 150);
    box-shadow: 0 0 0 1px var(--colorShadeB), 0 2px 0 0 var(--colorShadeB);
}

button.disable {
    cursor: default;
    background: rgb(190, 190, 190);
    background: var(--colorShadeC);
}

.example {
    margin: 18px 0 24px;
    display: flex;
    justify-content: space-between;
}

.example div {
    width: 50%;
    margin: 0 15px;
}

.example pre {
    line-height: 1.5;
}

